
import HeaderBack from '../Common/HeaderBack'
import OrderClick from '../Common/OrderClick'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
myHeader: HeaderBack,
myOrder: OrderClick,
Tab,
TabItem,
Scroller,
LoadMore,
},
data () {
return {
header: {
HBL_icon:"icon_back",
HBL_type: 'orderList',
HBC_text:"我的订单",
HBR_icon: "icon_edit",
HBR_show: false,
},
cur: '全部订单',

pullup:{
config: {
content: '上拉刷新',
pullUpHeight: 40,
height: 40,
autoRefresh: false,
downContent: '释放加载',
upContent: '上拉刷新',
loadingContent: '',
clsPrefix: 'xs-plugin-pullup-'
},
onFetching: false,
isAll: false,
isLoadMore: true,
isShow: false
},
bottomCount: 5,
orderItems:[],//页面展示订单列表
showCount:10,//每页显示的条数
currentPage:1,//页码从第一页开始
orderListIsEmpty: false,//是否为空

lastPage: false,
}
},
methods: {
onItemClick (index) {
var self = this;
self.$refs.isAll.style.display = 'none';
self.orderItems = [];
self.currentPage = 1;
self.$nextTick(() => {
self.$refs.scroller.reset({
top: 0
});
self.$refs.scroller.donePullup();
})
if(index == 0){
self.cur = '全部订单';
this.orderItems = [];
self.ajax();
this.$router.push({ path: '/mainpage/self/orderlist/0'});
} else if(index == 1){
self.cur = '待付款';
this.orderItems = [];
self.ajax();
this.$router.push({ path: '/mainpage/self/orderlist/1'});
} else if(index == 2){
self.cur = '待收货';
this.orderItems = [];
self.ajax();
this.$router.push({ path: '/mainpage/self/orderlist/2'});
} else if(index == 3){
self.cur = '待评价';
this.orderItems = [];
self.ajax();
this.$router.push({ path: '/mainpage/self/orderlist/3'});
} else if(index == 4){
self.cur = '退款/售后';
this.orderItems = [];
self.ajax();
this.$router.push({ path: '/mainpage/self/orderlist/4'});
}
},

onScrollBottom () {
},
onScroll (pos) {
},
pullupLoading () {
var self = this;
if (!self.pullup.onFetching) {
self.pullup.onFetching = true;
self.pullup.isLoadMore = false;
console.log(this.currentPage)
setTimeout(() => {
this.currentPage++;
self.$nextTick(() => {
this.ajax();
self.$refs.scroller.reset();
self.$refs.scroller.donePullup();
});
self.pullup.onFetching = false;
self.pullup.isLoadMore = true;
}, 2000);
}
},
ajax(){
let current = this.cur, orderState = '';
let orderList = this.$store.state.allOrder;
if(current == '全部订单'){
orderState = '/100'
}else if(current == '待付款'){
orderState = '/0'
}else if(current == '待收货'){
orderState = '/1'
}else if(current == '待评价'){
orderState = '/25'
}else if(current == '退款/售后'){
orderState = '/30'
}

comTools.showLoading(this, '载入中...');
setTimeout((data) => {

comTools.hideLoading(this);

this.pullup.isShow = true;
this.orderListIsEmpty = false;



orderList.forEach((element,index,arr) => {
if (orderState == '/100') {
this.orderItems.push(element);
} else {
if (element.status == orderState.replace('/', '')) {
this.orderItems.push(element);
}
}

});

if(this.lastPage){
this.$refs.isAll.style.display = 'block';
this.$refs.scroller.disablePullup();
} else {
this.$refs.isAll.style.display = 'none';
this.$refs.scroller.enablePullup();
}

}, 500)
},
},
mounted () {
var self = this;
var orderType  = this.$route.params.type;
setTimeout(()=>{
self.onItemClick(orderType);
},0)


setTimeout(function () {
comTools.setHeight(1.4);
self.$refs.scroller.reset();
},0)

},
watch: {

},
created() {
let i = this.$route.params.type;
if (i == 1) {
this.cur = '待付款';
} else if (i == 2) {
this.cur = '待收货';
} else if (i == 3) {
this.cur = '待评价';
} else if (i == 4) {
this.cur = '退款/售后';
}

this.ajax();
},
}



