export default {
  name: "orderclick",
  props:['value'],
  data() {
    return {
      id: this.value.id,
      type: this.value.type,
      status: this.value.status,
      productList: this.value.productList,
      payAccount: this.value.payAccount,
      createTime: this.value.createTime,
    }
  },
  methods: {
    goToDetail () {
      let orderId = this.id;
      console.log('列表跳转'+orderId);
      this.$router.push({path:"/mainpage/self/orderdetail/"+orderId});
    },
    // 取消订单
    sendCancelFn () {
      const _this = this;
      this.$vux.confirm.show({
        // content: '确定取消当前订单吗？',
        title: '确定取消当前订单吗？',
        onConfirm () {
          let orderId = _this.id;
          // 开始动画
          comTools.showLoading(_this,'取消订单中...');
          setTimeout((data) => {
            // 结束动画
            comTools.hideLoading(_this);
            if(true){
              _this.$vux.toast.show({
                type:'success',
                text:'取消成功',
                width: '7.5em'
              });
            }
          }, 500)
        }
      });
    },
    // 支付订单
    sendPayFn () {
      let orderId = this.id;
      // 数据加载
      comTools.showLoading(this,'支付中...');
      setTimeout((data) => {
        // 数据加载结束动画
        comTools.hideLoading(this);
        // 走支付流程
        this.weixinpay();
      }, 500)
    },
    weixinpay() {
      let orderId = this.id;
      if (Math.random()*10 > 3) {
        this.$router.push({path:'/payMent/success/' + orderId });
      } else {
        this.$router.push({path:'/payMent/fail/' + orderId });
      }
    },
    // 查看物流
    sendLogistics() {
      // 物流详情页面跳转
      let value = {
        orderId: this.id,
        productList: this.productList
      }
      this.$store.commit('setData', { key: 'orderObj', value: value });
      this.$router.push({ path: '/mainpage/self/logistics' });
    },
    // 确认收货
    sendReceiptFn () {
      const _this = this;
      this.$vux.confirm.show({
        // content: '确定收到检测用品吗？',
        title: '确定收到检测用品吗？',
        onConfirm() {
          // 确定
          let orderId = _this.id;
          let value = {
            orderId: orderId,
            productList: _this.productList
          }
          // 开始动画
          comTools.showLoading(_this, '确认中...');
          setTimeout((data) => {
            // 结束动画
            comTools.hideLoading(_this);
            if (true) {
              _this.$vux.toast.show({
                type: 'success',
                text: '确认成功',
                width: '7.5em'
              });
              _this.$router.push({ path: '/mainpage/self/evaluate' });
            }
          })
        }
      });
    },
    // 去评价
    sendEvaluateFn () {
      // 评价页面跳转
      let value = {
        orderId: this.id,
        productList: this.productList
      }
      this.$store.commit('setData', { key: 'orderObj', value: value });
      this.$router.push({ path: '/mainpage/self/evaluate' });
    },
    // 退款售后
    sendRefundFn () {
      let orderId = this.id;
      console.log('列表跳转'+orderId);
      this.$router.push({path:"/mainpage/self/orderdetail/"+orderId});
    },
  }
};



